<template>
  <div class="flex flex-col items-center justify-start">
    <div class="pt-6 mb-2 leading-none">
      <h1 class="fw-700 text-xl">Mis Bitácoras</h1>
      <h1 class="fw-600 text-xs">Tienes (7) parámetos asignados</h1>
      <div class="flex flex-row items-center justify-center py-2">
        <div
          class="w-2 h-2 rounded-full"
          :class="[conexion ? 'bg-green-500' : 'bg-red-500']"
        ></div>
        <h1
          class="ml-1 text-xxs uppercase fw-800"
          :class="[conexion ? 'text-green-500' : 'text-red-500']"
        >
          {{ conexion ? "ONLINE" : "OFFLINE" }}
        </h1>
      </div>
      <!-- FILTROS DE LAS BITACORAS -->
      <div class="w-full py-3">
        <div
          class="
            bg-white
            rounded-t-lg
            py-4
            px-4
            flex flex-col
            md:flex-row
            md:items-end
            items-center
            justify-center
            md:justify-center
            flex-wrap
            mb-4
            md:mb-0
          "
        >
          <div class="md:mr-4 mx-0 w-11/12 md:w-auto mb-3 md:mb-0">
            <h1 class="font-bold text-xxs uppercase text-left">
              Tipo de activo:
            </h1>
            <select
              class="
                bg-gris-30
                trans
                rounded-md
                px-3
                h-8
                focus:outline-none
                w-full
                md:w-auto
              "
              v-model="TipoEquipo"
              @change="filtroArrayParametros(TipoEquipo)"
            >
              <option value="TODOS">TODOS</option>
              <option v-for="(x, y) in arrayTiposEquipos" :key="y" :value="x">
                {{ x }}
              </option>
            </select>
          </div>

          <div
            class="
              relative
              flex
              items-center
              w-full
              md:w-auto
              px-10
              md:px-0
              mb-2
              md:mb-0
              mr-0
              md:mr-4
            "
          >
            <input
              type="seach"
              class="
                bg-gris-30
                trans
                w-full
                md:w-auto
                rounded-l-md
                pl-6
                h-8
                focus:outline-none
              "
              placeholder="Buscar..."
            />
            <svg
              xmlns="http://www.w3.org/2000/svg"
              class="w-4 h-4 absolute top-0 mt-2 ml-1 text-gris-600"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              stroke-width="3"
              stroke-linecap="round"
              stroke-linejoin="round"
            >
              <circle cx="11" cy="11" r="8"></circle>
              <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
            </svg>
            <button
              class="
                focus:outline-none
                bg-bluedemon-200
                rounded-r-md
                h-8
                w-8
                flex
                items-center
                justify-center
              "
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                class="w-4 h-4 text-bluedemon-400"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                stroke-width="3"
                stroke-linecap="round"
                stroke-linejoin="round"
              >
                <circle cx="11" cy="11" r="8"></circle>
                <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
              </svg>
            </button>
          </div>
          <div class="mx-0 w-10/12 md:w-auto">
            <button
              class="
                bg-yellow-200
                text-yellow-500
                h-8
                rounded-lg
                px-2
                text-xs
                w-10/12
                md:w-auto
                fw-500
                uppercase
              "
              v-if="conexion"
            >
              Historico
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- FILA PARA HOY -->
    <div class="contbotones">
      <div class="seccion">
        <div class="py-2">
          <h1 class="font-semibold">
            PARA HOY
            <span class="font-bold">()</span>
          </h1>
        </div>

        <Entradabit
          v-for="(x, y) in arrayParametros"
          :key="y"
          :array="x"
          :indexPadre="y"
          tipo="HOY"
        />
      </div>

      <!-- FILA PENDIENTES -->
      <div class="seccion">
        <div class="py-2">
          <h1>Pendientes <span class="font-bold text-xs">()</span></h1>
        </div>
        <Entradabit
          v-for="(x, y) in arrayParametros"
          :key="y"
          :array="x"
          :indexPadre="y"
          tipo="PENDIENTES"
        />
      </div>

      <!-- FILA LLENOS -->
      <div class="seccion">
        <div class="py-2">
          <h1>Llenos <span class="font-bold text-xs">(1)</span></h1>
        </div>
        <Entradabit
          v-for="(x, y) in arrayParametros"
          :key="y"
          :array="x"
          :indexPadre="y"
          tipo="LLENOS"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { computed, onMounted } from "@vue/runtime-core";
import Entradabit from "../components/Entradabit.vue";
import { useStore } from "vuex";
import { ref } from "@vue/reactivity";

export default {
  components: { Entradabit },
  setup() {
    const store = useStore();

    const Swal = require("sweetalert2");
    const Toast = Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });

    const pendientes = ref(0);
    const TipoEquipo = ref("TODOS");

    onMounted(() => {
      const status = async () => {
        try {
          const response = await store.dispatch("getConexion", {
            idDestino: localStorage.getItem("idDestino"),
            idUsuario: localStorage.getItem("usuario"),
            action: "conexion",
          });

          if (response) {
            store.state.conexion = true;
            getArray();
          }
        } catch (error) {
          store.state.conexion = false;
        }
      };
      status();
    });

    const getArray = () => {
      store.dispatch("getArray", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "getArray",
      });
    };

    const conexion = computed(() => {
      return store.state.conexion;
    });

    const arrayParametros = computed(() => {
      return store.state.arrayParametros;
    });

    const arrayTiposEquipos = computed(() => {
      const arrayNuevo = [];

      store.state.arrayTiposEquipos.forEach((element) => {
        if (!arrayNuevo.includes(element)) {
          arrayNuevo.push(element);
        }
      });

      return arrayNuevo;
    });

    const filtroArrayParametros = (tipoEquipo) => {
      // const arrayFull = store.state.arrayParametrosFull;
      // const arrayNuevo = store.state.arrayParametrosFull.forEach(
      //   (x, index, object) => {
      //     object.splice(index, 1);
      //     x.equipos.forEach((x, index, object) => {
      //       if (x.tipoEquipo === tipoEquipo || tipoEquipo == "TODOS") {
      //         console.log(x);
      //         object.splice(index, 1);
      //       }
      //     });
      //   }
      // );
      // console.log(arrayNuevo);
      // store.state.arrayParametros = arrayNuevo;
    };

    const subirDatos = async () => {
      try {
        const response = await store.dispatch("getConexion", {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          action: "conexion",
        });

        if (response && localStorage.getItem("dataOffline").length) {
          store.state.conexion = true;
          const array = JSON.parse(localStorage.getItem("dataOffline"));

          if (!array.length) {
            getArray();
            return;
          }

          array.forEach(async (data, index) => {
            const response = await store.dispatch("getCaptura", data);

            if (response) {
              if (index + 1 == array.length) {
                Swal.fire(
                  "Conectado a Internet",
                  `Datos Capturados: ${index + 1} De: ${array.length}`,
                  "success"
                );
                localStorage.setItem("dataOffline", "");
                getArray();
              }
              return;
            }
          });
          return;
        }
      } catch (error) {
        console.log(error);
      }
    };

    window.addEventListener(
      "online",
      function () {
        subirDatos();
        store.state.conexion = true;
        // Swal.fire("Con Acceso a Internet", `Modo: ONLINE`, "info");
      },
      false
    );

    window.addEventListener(
      "offline",
      function () {
        store.state.conexion = false;
        // Swal.fire("Sin Acceso a Internet", `Modo: OFFLINE`, "info");
      },
      false
    );

    return {
      conexion,
      arrayParametros,
      arrayTiposEquipos,
      pendientes,
      TipoEquipo,
      filtroArrayParametros,
    };
  },
};
</script>

<style scoped>
.contbotones {
  scroll-snap-type: x mandatory;
  overflow-x: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  @apply w-full flex flex-row justify-center items-start px-2;
}

.contbotones::-webkit-scrollbar {
  /* WebKit */
  width: 0;
  height: 0;
}
.seccion {
  scroll-snap-align: center;
  flex: none;
  width: 100%;
}

@media (min-width: 768px) {
  .seccion {
    width: 33.333333%;
  }
}
</style>
