import { createStore } from 'vuex'

export default createStore({
  state: {
    arrayParametros: [],
    arrayParametrosFull: [],
    arrayTiposEquipos: [],
    conexion: false,
  },

  mutations: {

    setArrayParametros(state, payload) {
      state.arrayParametros = payload;
    },

    setArrayParametrosFull(state, payload) {
      state.arrayParametrosFull = payload;
    },

    setArrayTiposEquipos(state, payload) {
      state.arrayTiposEquipos = payload;
    },

  },

  actions: {

    // OBTIENE DATOS DEL ARRAY
    async getArray({ commit }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          localStorage.setItem("offline", JSON.stringify(data));
          commit("setArrayParametros", data.data);
          commit("setArrayParametrosFull", data.data);
          commit("setArrayTiposEquipos", data.tiposEquipos);
          return true;
        }

        return false;
      } catch (error) {
        return false;
      }
    },

    // COMPRUEBA LA CONEXIÓN
    async getConexion({ commit, state }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          return await data.conexion;
        }
        return false;

      } catch (error) {
        const array = JSON.parse(localStorage.getItem("offline"));
        commit("setArrayParametros", array.data);
        commit("setArrayParametrosFull", array.data);
        commit("setArrayTiposEquipos", array.tiposEquipos);
        return false;
      }
    },


    // CAPTURA DATOS DE LA BITACORA
    async getCaptura({ commit, state }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          return await data.conexion;
        }
        return false;
      } catch (error) {
        const array = JSON.parse(localStorage.getItem("offline"));
        array.data = state.arrayParametros;
        localStorage.setItem("offline", JSON.stringify(array));
        return false;
      }
    },

  },

  modules: {
  }
})
